  }),
  metaInfo: {
    title: 'Top 10 truyện cổ tích hay và ý nghĩa nên đọc cho bé mầm non - Thư Viện',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_1589227231_truyencotichveloaivatconluahat.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'truyện cổ tích, truyện cổ tích hay, truyện cổ tích ý nghĩa, truyện cổ tích cho bé' },
